<template>
	<div class="table_list">

		<!--头部搜索和按钮-->
		<div class="search clearfix">
			<!--头部搜索-->
			<div class="btn left clearfix btn_search">
				<!--餐桌类型-->
				<span>餐桌区域</span>
				<el-select allow-create v-model="table_region" placeholder="全部">
					<el-option label="全部" value="全部">
					</el-option>
					<el-option v-for="item in  tableData " :label="item" :value="item" :key="item">
					</el-option>
				</el-select>
				<el-input placeholder="请输入名称或编号" v-model="num"></el-input>

				<!--搜索按钮-->
				<button class="blue search_button" @click="handleFilter">
					搜索
				</button>
			</div>

			<!--头部右边的按钮   新建，导出二维码，删除-->
			<div class="btn right clearfix">
				<!--创建-->
				<router-link :to="{path:'/table/edit'}">
					<button class="blue create_button">
						新建
					</button>
				</router-link>
				<!--导出二维码-->
				<button class="blue export_button" @click="handleDownload">
						<span v-if="!multipleSelection.length">导出二维码</span>
						<a :href="downLoad" class="downLoad" v-if="multipleSelection.length">导出二维码</a>
				</button>
				<!--删除-->
				<button class=" red delete_button" @click="deleteDatasInBtnGroup()">
					删除
				</button>
			</div>
		</div>

		<!--列表-->
		<div class="table_info">
			<el-table :data="tableList" @selection-change="selectItem" style="width: 100%" stripe height="680" ref="tableList" @select="selectRow">
				<el-table-column type="selection" min-width="5%">
				</el-table-column>
				<el-table-column prop="seat_id" label="编号" align="center" min-width="10%">
				</el-table-column>
				<el-table-column prop="seat_name" label="餐桌台号" align="center" min-width="10%">
				</el-table-column>
				<el-table-column prop="seat_region" label="餐桌区域" align="center" min-width="10%">
				</el-table-column>
				<el-table-column prop="seat_type" label="餐桌类型" align="center" min-width="10%">
				</el-table-column>
				<el-table-column prop="seat_shape" label="桌型" align="center" min-width="10%">
				</el-table-column>
				<el-table-column prop="seat_size" label="可供就餐人数" align="center" min-width="10%">
				</el-table-column>
				<el-table-column prop="price" label="餐位费" align="center" min-width="10%">
					<template slot-scope="scope">
						<span style="margin-left: 10px">{{scope.row.price}}</span>
						<span v-if="scope.row.price_type==type.PEOPLENUM">元/人</span>
						<span v-else-if="scope.row.price_type==type.FIXED">元/桌</span>
						<span v-else-if="scope.row.price_type==type.PERCENTAGE">%/单</span>
						<span v-else></span>
					</template>
				</el-table-column>
				<el-table-column label="二维码" inline-template align="center" min-width="10%" class="code">
					<span class="blue" type="text" size="small" @click="getQR($index,row)"><span>点击预览</span></span>
				</el-table-column>
				<el-table-column label="操作" inline-template align="center" min-width="15%">
					<div class="btng">
						<span class="blue" type="text" size="small">
					
					<router-link :to="{path:'/table/edit',query:{id:row.seat_id}}">
					编辑
					</router-link>
					
				</span>
						<span class="red" type="text" size="small" @click="confirmDelete(row)">
					删除
				</span>

					</div>
				</el-table-column>
			</el-table>
			<div class="data-empty" v-if="isListEmpty">
				<div class="img">
					<img src="../../../assets/images/empty.png">
				</div>
				<div class="text">
					<span v-if="searchNone==false">暂无数据...</span>
					<span v-if="searchNone==true">暂无搜索数据...</span>
				</div>

			</div>
			<!--分页弹框-->
			<div class="pagination-container">
				<el-pagination small @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="listQuery.page" :page-sizes="[20,30,50]" :page-size="listQuery.limit" layout="sizes, prev, pager, next, jumper" :total="total">
				</el-pagination>
			</div>
		</div>

		<!--删除信息弹框-->

		<div class="messageBox" v-if="deleted">
			<div class="deleteMessage">
				<div class="img"></div>
				
				
				
				<div class="tableInfor" v-if="success_delete==false"><span class="question"></span><div class="delete_text">确定删除餐桌<span v-for="(id,index) in multipleSelection"v-if="index<3">{{id.seat_id}} <span v-if="multipleSelection.length>1&&index<2"> 、 </span><span v-if="index==2"> ...</span></span>的信息？</div></div>
				<p class="tableInfor" v-if="success_delete==true"><span class="success-delete"></span><span class="delete_text">删除成功</span></p>

				<div class="btn" v-if="success_delete==false">
					<el-button :plain="true" @click="confirm" class="confirmBtn">确定</el-button>
					<el-button @click="cancel">取消</el-button>
				</div>
				<div class="btn" v-if="success_delete==true">
					<el-button :plain="true" @click="successDelete" class="confirmBtn deletes">确定</el-button>
				</div>
			</div>
		</div>

		<!--点击预览二维码-->
		<div class="showQR" v-show="showQr">
			<div class="qrbox">
				<div class="QRbgImg">
					<span class="QR"> <img :src="imgsrc" class="img"/></span>
					<p class="qrTableName">桌号 {{qrTableName}}</p>
					<p class="shopName">{{shopName}}</p>

				</div>
				<p class="textTip1">提供贴纸式、立牌式等各种桌牌形式，如果有需要，</p>
				<p class="textTip2">请联系客服 ：<span class="textBlue">0755-2306 0180</span></p>

			</div>
			<span class="closeBtn" @click="closeQr"></span>
		</div>
	</div>
</template>

<script>
	import { apiMethods } from '../api';
	import { commonMethods } from '../index';
	import { Public } from '@/config/public';
	import { ls } from "@/config/pageStore";
	import { seatPriceType } from "@/config/cfg";
	export default {
		data() {
			return {
				shopName: "",
				isChecked:[],
				qrTableName: "",
				downLoad: "", //下载链接
				deleted: false, //显示删除弹框
				showQr: false, //预览二维码开关
				type: "", //餐位费支付方式
				num: "", //名称或编号
				shuopId: "",
				imgsrc: "",
				isListEmpty: true, //暂无数据
				success_delete: false,
				table_region: "", //餐桌区域
				deleteNo: "", //单个删除的编号
				total: 10, //总页数
				searchNone:false,
				options: [], //搜索中编号和名称对应项的集合
				tableData: [], //餐桌列表信息(搜索下拉框list)
				tableList: [], //列表中显示数据
				testList: [], //用于分页list过滤
				arr: [], //餐桌列表信息(搜索下拉框list)
				multipleSelection: [], //勾选的集合（多选）
				selectIndex: {}, //记录某页的勾选项
				tagInTableList: {}, //三个可编辑的标签历史选项，便于删除标签时查看
				listQuery: {
					page: 1,
					limit: 20
				},
			};
		},
		created() {
			this.initTableData();
			this.shuopId = Public.getShopid();
			let shopinfo = Public.getShopinfo();
			shopinfo.shopinfo.forEach((item) => {
				if(item.shop_id == this.shuopId) {
					this.shopName = item.shop_name
				}
			})
		},
		methods: {
			//初始化表格信息
			initTableData() {
				//获取每页选择显示的条目数的历史记录
				if(ls.getItem("itemNum") != null) {
					this.listQuery.limit = Number(ls.getItem("itemNum"));
				}
				//获取每页选择页面的历史记录
				//				if(sessionStorage.getItem("pageNum") != null) {
				//					this.listQuery.page = Number(sessionStorage.getItem("pageNum"));
				//				}
				//初始化表格
				apiMethods.getTableData({
					get_seat_list: 1
				}, (resp) => {
					if(resp.ret == 0) {
						this.tableList = resp.data.seatlist||[];
						this.testList = resp.data.seatlist||[];
						if(this.tableList!==undefined&&this.tableList.length !== 0) {
							this.isListEmpty = false
							this.searchNone =false
						} else {
							this.searchNone =false
							this.isListEmpty = true
						}
						this.total = commonMethods.getTotal(this.testList);
						this.tableList = commonMethods.getPageList(this.testList, this.listQuery.limit, this.listQuery.page)||[];
						this.type = seatPriceType;
						this.tagInTableList = apiMethods.getUniqueList(resp.data.seatlist);
						ls.setItem("hisTagObj", this.tagInTableList);
						this.tableData = apiMethods.objTansToArr(this.tagInTableList)[0];
						let arr1 = [],
							arr2 = [];
						arr1 = apiMethods.objTansToArr(this.tagInTableList)[4];
						arr2 = apiMethods.objTansToArr(this.tagInTableList)[3];
						this.arr = apiMethods.getUniqueType(arr1, arr2);
					}
				});

			},
			//复选框
			selectItem(val) {
				this.multipleSelection = val;
				this.getSelectedIds();
				this.downLoad = base_url + "/img_get.php?batch_export_seat_qrcode=1&seat_list=" + encodeURIComponent(JSON.stringify(this.getSelectedIds())) + "&shop_id=" + this.shuopId;

			},
			//获取选中的列表的id
			getSelectedIds() {
				let array = [];
				this.multipleSelection.forEach((res) => {
					array.push(res.seat_id);
				});
				return array;
			},
			//单个删除
			confirmDelete(item) {
				this.deleted = true;
				this.multipleSelection = [item];
			},
			//点击确定
			confirm() {
				apiMethods.changeTableData({
					seat_delete: 1,
					seat_id_list: JSON.stringify(this.getSelectedIds())
				}, (resp) => {
					if(resp.ret == 0) {
						this.initTableData();
						this.multipleSelection = [];
						this.success_delete = true
					} else if(resp.ret == -30034) {
						this.$slnotify({
							message: '该账号已经被冻结'
						});
						this.$router.push('/');
					}
				});

			},
			//点击删除成功之后的确定
			successDelete() {
				this.deleted = false;
				this.success_delete = false;
			},
			//选择每一行，记录页面上面的选中项
			selectRow(selection, row) {
				this.isChecked.push(row);
				var arr = [];
				this.testList.forEach((r, index) => {
					for(var i = 0; i < this.testList.length; i++) {
						if(r == selection[i]) {
							arr.push(index);
						}
					}
				});
				this.selectIndex[this.listQuery.page] = arr;
			},
			//点击取消
			cancel() {
				if(this.multipleSelection!==[]&&this.isChecked.length==0) {
					this.multipleSelection = [];
				}
				this.shuopId = Public.getShopid();
				this.deleted = false;
			},
			//多选删除
			deleteDatasInBtnGroup() {
				if(!this.multipleSelection.length) {
					this.$slnotify({
						message: "您还没有勾选任何选项  !"
					});
					return;
				}
				this.deleted = true;
			},
			//获取二维码
			getQR(index, row) {
				let shopid = Public.getShopid();
				this.qrTableName = row.seat_name
				this.showQr = true;
				this.imgsrc = base_url + "/img_get.php?get_seat_qrcode=1&shop_id=" + shopid + "&seat_id=" + row.seat_id;
			},
			//点击关闭二维码
			closeQr() {
				this.showQr = false;
			},
			//根据输入框的值进行搜索
			remoteMethod(query) {
				if(query !== '') {
					setTimeout(() => {
						this.options = this.arr.filter(item => {
							return item.indexOf(query) > -1;
						});
					}, 500);
				} else {
					query = "";
					this.options = [];
				}

			},
			//点击搜索
			handleFilter() {
				this.total = commonMethods.getTotal(this.testList);
				this.tableList = apiMethods.search(this.table_region, this.num, this.testList);
				if(this.tableList!==undefined&&this.tableList.length !== 0) {
							this.isListEmpty = false
							this.searchNone =false
						} else {
							this.isListEmpty = true
							this.searchNone =true
						}
			},

			//选择每页显示的条目数
			handleSizeChange(val) {
				this.listQuery.limit = val;
				ls.setItem("itemNum", this.listQuery.limit);
				this.tableList = commonMethods.getPageList(this.testList, this.listQuery.limit, this.listQuery.page);
				this.total = commonMethods.getTotal(this.testList);
			},
			//选择当前页码
			handleCurrentChange(val) {
				this.listQuery.page = val;
				this.total = commonMethods.getTotal(this.testList);
				this.tableList = commonMethods.getPageList(this.testList, this.listQuery.limit, this.listQuery.page);
				var allSelectIndex = this.selectIndex[val];
				var indexArr = [];
				if(allSelectIndex !== undefined) {
					for(var i in this.selectIndex[val]) {
						let index = Number(this.selectIndex[val][i] + 1);
						$(".el-checkbox__input").eq(index).addClass("is-checked");
					}
				}
				//				sessionStorage.setItem("pageNum", this.listQuery.page);
			},
			//点击导出
			handleDownload() {
				if(!this.multipleSelection.length) {
					this.$slnotify({
						message: "您还没有勾选任何选项  !"
					});
					return;
				} else {
					$(".downLoad").attr("qrcode", "download");
					this.downLoad = base_url + "/img_get.php?batch_export_seat_qrcode=1&seat_list=" + encodeURIComponent(JSON.stringify(this.getSelectedIds())) + "&shop_id=" + this.shuopId;
					this.$router.push("/table");
				}
			},

		},
	};
</script>

