<style lang="scss">
	@import 'src/styles/mixin.scss';
	.table_list {
		.btn {
			.el-option {
				height: 34px;
			}
			.el-select {
				width:180px;
				margin-left: 10px;
				margin-right: 20px;
			}
		
			.el-select-dropdown {
				min-width: 181px !important;
			}
			.el-input {
				width: 180px;
				height: 34px;
				margin-right: 20px;
			}
		}
		.el-table thead .cell {
			font-weight: 900 !important;
		}
		.deleteMessage {
			.el-button {
				width: 160px;
				height: 40px;
				margin-right: 60px;
				color: #4877E7;
				border: 1px solid #4877E7;
			}
			.confirmBtn.el-button {
				background: #4877E7;
				color: #FFFFFF;
			}
		}
		.el-table__empty-text {
			display: none;
		}
	}
</style>


